import React from 'react'

import { connect } from 'react-redux'


import io from 'socket.io-client'

import MainComp from '../Container/Main/'

import {CHANGE_LANGUAGE,UPDATE_CARS} from '../../actions'

const mapStateToProps = (state) => {
    return {
        lang: state ? state.lang : "En",
        cars: state ? state.cars : []
    }
}


const mapdispatchToProps = (dispatch) => {
    return {
        changeLanguage: (lang) => dispatch({ type: CHANGE_LANGUAGE, data: { lang } }),
        fetchCars: () => {

            // let socket = io('http://localhost:8080')

            // socket.on('UpdateVechiles', (data) => {
            //     dispatch({
            //         type: UPDATE_CARS,
            //         data
            //     })
            // })


            let data=[{
                "vin":"YS2R4X20005399401",
                "regnr":"ABC123",
                "owner":"Kalles Grustransporter AB ",
                "status":1
            }]

            for(let counter=0;counter<20;++counter)
            {
                data.push({
                    "vin":"YS2R4X20005399401",
                    "regnr":"ABC123",
                    "owner":counter%2?"Kalles Grustransporter AB ":"alaa hamed",
                    "status":counter%2?1:0
                })
            }

            dispatch({
                type: UPDATE_CARS,
                data
            })
        }
    }
}

export default connect(mapStateToProps, mapdispatchToProps)(MainComp)
